	const click_handler_2 = i => {
		finalAnswer(i);
	};

	function dialog1_open_binding(value) {
		firstAnswered = value;
		$$invalidate(1, firstAnswered);
	}

	const close_handler_1 = () => {
		$$invalidate(1, firstAnswered = false);
	};

	function dialog2_open_binding(value) {
		checkTheAnswer = value;
		$$invalidate(2, checkTheAnswer);
	}

	function dialog3_open_binding(value) {
		resultDialog = value;
		$$invalidate(8, resultDialog);
	}

	return [
		gameStart,
		firstAnswered,
		checkTheAnswer,
		isCorrect,
		openDoor,
		firstAnswer,
		notChangeCorrect,
		changeCorrect,
		resultDialog,
		selectFirstAnswer,
		finalAnswer,
		gameClear,
		stagingMode,
		click_handler,
		click_handler_1,
		dialog0_open_binding,
		close_handler,
		click_handler_2,
		dialog1_open_binding,
		close_handler_1,
		dialog2_open_binding,
		dialog3_open_binding
	];
}

class Game extends SvelteComponent {
	constructor(options) {
		super();
		init(this, options, instance, create_fragment, safe_not_equal, {});
	}
}

export default Game;